.settings_btn {
    box-sizing: border-box;
    display: inline-block;
    min-width: 1.5em;
    min-height: 37px;
    padding: 0.5em 1em;
    margin-left: 10px;
    margin-top: 4px;
    text-align: center;
    text-decoration: none !important;
    cursor: pointer;
    *cursor: hand;
    color: #333 !important;
    border: 1px solid rgb(177, 177, 177);
    border-radius: 2px;
    background-color: rgb(248, 248, 248);
  
  }
  
  .settings_btn:hover {
    color: rgb(34, 34, 34) !important;
    border: 1px solid rgb(177, 177, 177);
    background-color: rgb(200, 200, 200);
  
  }